/* ==========================================================================
   #FONT-FACE
   ========================================================================== */

/**
 * Add font-display: swap to font-face blocks here
 * https://www.zachleat.com/web/comprehensive-webfonts/#font-display
 */

@font-face {
  font-family: LatoSubset;
  src: url('data:application/x-font-woff;charset=utf-8;base64,')
    format('woff');
  font-weight: 400;
  font-style: normal;
  unicode-range: U+65-90, U+97-122;
}

@font-face {
  font-family: Lato;
  src: url('../../assets/fonts/lato-regular-webfont.woff2') format('woff2'),
    url('../../assets/fonts/lato-regular-webfont.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: Lato;
  src: url('../../assets/fonts/lato-regular-webfont.woff2') format('woff2'),
    url('../../assets/fonts/lato-regular-webfont.woff') format('woff');
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: LatoBold;
  src: url('../../assets/fonts/lato-bold-webfont.woff2') format('woff2'),
    url('../../assets/fonts/lato-bold-webfont.woff') format('woff');
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: LatoItalic;
  src: url('../../assets/fonts/lato-italic-webfont.woff2') format('woff2'),
    url('../../assets/fonts/lato-italic-webfont.woff') format('woff');
  font-weight: 400;
  font-style: italic;
}

@font-face {
  font-family: LatoBoldItalic;
  src: url('../../assets/fonts/lato-bolditalic-webfont.woff2') format('woff2'),
    url('../../assets/fonts/lato-bolditalic-webfont.woff') format('woff');
  font-weight: 700;
  font-style: italic;
}
